
import {defineComponent,getCurrentInstance,ref,toRefs,reactive,onMounted,watch,computed} from 'vue';
import {useStore} from 'vuex'
import {useRoute,useRouter} from 'vue-router'
export default defineComponent({
	name: 'My',
	components: {},
	setup(props, context) {
		const router = useRouter();
		const navVisited = reactive([
			false,
			false
		]);
		const navRouter = reactive([
			'Cart',
			'Order'
		]);
		const isclick = (val:number)=>{
			let i = navVisited.indexOf(true);
			navVisited.splice(i,1,false);
			navVisited.splice(val,1,true);
			router.push({name:navRouter[val],params:{id:val}})
		}
		const goback = ()=>{
			router.back();
		}
		return{navVisited,isclick,goback}
	}
});
